const getCurrentDate = () => {
    let current_date = new Date();
}

const isLogin = (token_code) => {
    // 需要验证口令
    let code = localStorage.getItem('token') || false;
    return (code && code === token_code);
}

export default {
    getCurrentDate,
    isLogin
}